<script>
import { reactive, computed } from 'vue';
import vueTable from '@/shared/components/plugins/VueTable.vue';

export default {
    methods: {
        randomNo() {
            return Math.floor(Math.random() * 60) + 30;
        },
        randomTransactionType() {
            const types = ['Ausgabe', 'Einzahlung'];
            const randomIndex = Math.floor(Math.random() * types.length);
            return types[randomIndex];
        },
        randomStatusType() {
            const types = ['Abgeschlossen', 'In Bearbeitung', 'Gekündigt', 'Fehler'];
            const randomIndex = Math.floor(Math.random() * types.length);
            return types[randomIndex];
        },
        randomPaymentType() {
            const types = ['PayPal', 'Banküberweisung', 'CloudCoins', 'PaySafeCard'];
            const randomIndex = Math.floor(Math.random() * types.length);
            return types[randomIndex];
        },
    },
    data() {
        const data = reactive([]);
        for (let i = 1; i <= 12; i++) {
            data.push({
                id: i,
                datetime: "26.07.2023 14:42",
                action: this.randomTransactionType(),
                service: "vServer Linux (#" + this.randomNo() + ")",
                cost: this.randomNo() + ".00 €",
                payment: this.randomPaymentType(),
                status: this.randomStatusType(),
                bill: "Nicht verfügbar",
            });
        }

        // Table config
        const table = reactive({
            columns: [
                {
                    label: "Bestell-Nr.",
                    field: "id",
                    width: "5%",
                    sortable: true,
                    isKey: true,
                },
                {
                    label: "Datum",
                    field: "datetime",
                    width: "5%",
                    sortable: true,
                },
                {
                    label: "Dienst",
                    field: "service",
                    width: "10%",
                },
                {
                    label: "Aktion",
                    field: "action",
                    width: "20%",
                    display: function (row) {
                        if (row.action == "Ausgabe") {
                            return '<i class="fa-solid fa-arrow-trend-down text-danger"></i> ' + row.action;
                        } else if (row.action == "Einzahlung") {
                            return '<i class="fa-solid fa-arrow-trend-up text-success"></i> ' + row.action;
                        } else {
                            return row.action;
                        }
                    },
                },
                {
                    label: "Kosten",
                    field: "cost",
                    width: "20%",
                    display: function (row) {
                        if (row.action == "Ausgabe") {
                            return '-' + row.cost;
                        } else if (row.action == "Einzahlung") {
                            return '+' + row.cost;
                        } else {
                            return row.cost;
                        }
                    },
                },
                {
                    label: "Zahlungsmethode",
                    field: "payment",
                    width: "5%",
                },
                {
                    label: "Status",
                    field: "status",
                    width: "25%",
                    sortable: true,
                    display: function (row) {
                        if (row.status == "Abgeschlossen") {
                            return '<i class="fa-solid fa-circle-check text-success"></i> ' + row.status;
                        } else if (row.status == "In Bearbeitung") {
                            return '<i class="fa-solid fa-clock text-warning"></i> ' + row.status;
                        } else if (row.status == "Gekündigt") {
                            return '<i class="fa-solid fa-ban text-inverse"></i> ' + row.status;
                        } else if (row.status == "Fehler") {
                            return '<i class="fa-solid fa-circle-exclamation text-danger"></i> ' + row.status;
                        } else {
                            return row.status;
                        }
                    }
                },
                {
                    label: "Rechnung",
                    field: "bill",
                    width: "10%",
                },
            ],
            rows: data,
            totalRecordCount: computed(() => {
                return table.rows.length;
            }),
            sortable: {
                order: "id",
                sort: "desc",
            },
        });

        return {
            table
        }
    },
    components: {
        vueTable: vueTable
    }
}
</script>

<template>
    <card-element>
        <card-body class="align-items-center text-inverse m-5px bg-inverse">
            <div class="text-center">
                <div class="opacity-5">
                    <i class="fa fa-money-bill-wave fa-4x mb-2"></i>
                </div>
                <h4>Transaktionen</h4>
            </div>
            <vue-table class="vue-table" :is-static-mode="true" :columns="table.columns" :rows="table.rows"
                :total="table.totalRecordCount" :sortable="table.sortable" />
        </card-body>
    </card-element>
</template>